@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

*{
  font-family: 'Quicksand';
}

/* index.css */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Poppins'
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(30,30,30);
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
  
/* Navbar */
.navbar{
  background-color:rgb(196,30,58);
  width: 100%;
  padding: 0.75rem 0rem 0.75rem 0rem;
}

.navbar-button{
  font-size: 1rem;
  padding: 0.5rem 1rem 0.5rem 1rem;
  margin: 0.25rem 0.5rem 0.25rem 0.5rem;
  color: white;
  background-color: rgb(196,30,58);
  border: none;
  border-radius: 50px;
  transition-duration: 0.2s;
}
.navbar-button:hover{
  background-color:rgb(159, 26, 49);
  cursor: pointer;
}

#about{
  float: right;
}

/* Heading */
.heading{
  margin: 2rem auto 2rem auto;
  font-size: 2rem;
  text-align: center;
}

.heading-2{
  margin: 2rem auto 2rem auto;
  font-size: 1.5rem;
  text-align: center;
}

/* Home */
.get-started{
  font-size: 1.5rem;
  display: grid;
  margin: auto;
  padding: 0.5em 1em 0.5em 1em;
  border: none;
  border-radius: 50px;
  color: white;
  background-color: rgb(196,30,58);
  transition-duration: 0.2s;
}

.get-started:hover{
  cursor: pointer;
  background-color: rgb(159, 26, 49);
}

/* About Page */
.member-container{
  display: grid;
  border: white 1px solid;
  border-radius: 25px;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 1em auto 1em auto;
  width: 75%;
}

.member{
  display: block;
  padding: 1em;
  margin: 1em;
  transition-duration: 0.1s;
  border-radius: 25px;
}
.member:hover{
  background-color: rgb(45, 45, 45);
  cursor: pointer;
}

.member-photo{
  margin: 1em auto 1em auto;
  display: block;
  width: 7.5em;
  height: 7.5em;
  object-fit: cover;
  outline: white solid 1px;
}

.member-title{
  text-align: center;
}

/* Weekly Schedule + Study Plan */
.weekly-schedule{
  margin: 0 auto 5em auto;
  border: rgb(196,30,58) solid 2px;
}

.weekly-schedule-title{
  margin-top: 2em;
  font-size: 1.5rem;
  text-align: center;
}

.weekly-schedule_entry-heading, .weekly-schedule_entry{
  text-align: center;
  padding: 1em;
  border: rgb(196,30,58) solid 1px;
}   
  
.weekly-schedule_entry-heading{
  background-color: rgb(196,30,58);
}

/* Individual Member Pages*/
.info-container{
  display: flex;
  flex-direction: column;
  margin: 1em auto 1em auto;
  width: 50%;
}

.info-photo{
  margin: 1em auto 1em auto;
  display: block;
  width: 25%;
  outline: black solid 3px;
}

.info-text{
  white-space: pre-line;
  width: 75%;
  margin: 1em auto 1em auto;
  text-align: center;
}

/* Selection Pages */
.next-button{
  display: grid;
  padding: 0.5em 2em 0.5em 2em;
  margin: 1em auto 1em auto;
  font-size: 1.5rem;
  border: none;
  border-radius: 50px;
  color: white;
  background-color: rgb(196,30,58);
  transition-duration: 0.2s;
}

.next-button:hover{
  cursor: pointer;
  background-color: rgb(159, 26, 49);
}

.test{
  text-align: center;
}

/* Select Genre */
.genre-button-container{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  width: 75%;
  margin: auto;
  border: 1px white solid;
  border-radius: 50px;
  padding: 1em;
}

.genre-button-off, .genre-button-on{
  transition-duration: 0.2s;
  padding: 1em 2em 1em 2em;
  font-size: 1.5rem;
  border-radius: 50px;
  margin: 0.25em;
}

.genre-button-off{
  background-color: rgb(30,30,30);
}

.genre-button-off:hover{
  background-color: rgb(45, 45, 45);
  cursor: pointer;

}

.genre-button-on{
  background-color: rgb(45, 45, 45);
}

.genre-button-on:hover{
  background-color: rgb(60, 60, 60);
  cursor: pointer;
}

.genre-button-checkbox{
  display: none;
}

.genre-button-text{
  font-size: 1.5rem;
  margin: auto;
  color: white;
  text-align: center;
}

/* Select Year */
.year-button-container, .runtime-button-container, .age-button-container{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  width: 75%;
  margin: auto;
  border: 1px white solid;
  border-radius: 25px;
  padding: 1em;
}

.year-button-on, .year-button-off, .runtime-button-on, .runtime-button-off, .age-button-on, .age-button-off{
  transition-duration: 0.2s;
  padding: 5em 1em 5em 1em;
  font-size: 1.5rem;
  border-radius: 25px;
  margin: 0.25em;
}

.year-button-checkbox, .runtime-button-checkbox, .age-button-checkbox{
  display: none;
  opacity: 0;
  width: 0px;
  height: 0px;
}

.year-button-off, .runtime-button-off, .age-button-off{
  background-color: rgb(30,30,30);
}

.year-button-off:hover, .runtime-button-off:hover, .age-button-off:hover{
  background-color: rgb(45,45,45);
  cursor: pointer;

}

.year-button-on, .runtime-button-on, .age-button-on{
  background-color: rgb(45,45,45);
}

.year-button-on:hover, .runtime-button-on:hover, .age-button-on:hover{
  background-color: rgb(60,60,60);
  cursor: pointer;
}

.year-button-text, .runtime-button-text, .age-button-text{
  margin: auto;
  color: white;
  text-align: center;
}

.year-button-desc-text, .runtime-button-desc-text, .age-button-desc-text{
  font-size: 0.75em;
  text-align: center;
}

/* My List */
.error{
  margin: 1em auto 1em auto;
  text-align: center;
}

.movie-container{
  display: flex;
  margin: 1em auto 1em auto;
  padding: 0.5em;
  border: 1px white solid;
  border-radius: 25px;
  width: 50%;
}

.movie-title{
  font-size: 2em;
  margin: 0.5em 0em 0.5em 0em;
  padding: 0em 0.5em 0em 0.5em;
}
.movie-desc{
  padding: 0em 1em 0em 1em;
}

.movie-rating-container{
  display: flex;
  flex-direction: column;
  margin: 1em 1em 1em auto;
}

.like-off, .dislike-off, .like-on, .dislike-on{
  transition-duration: 0.2s;
  border-radius: 25px;
  margin: 0.25em;
  padding: 0.5em;
}

.like-off:hover {
  background-color: rgb(100, 150, 181);
  cursor: pointer;
}

.dislike-off:hover {
  background-color: rgb(181, 99, 99);
  cursor: pointer;
}

.like-on{
  background-color: rgb(135, 206, 250);
}

.dislike-on{
  background-color: rgb(240, 128, 128);
}

.movie-rating-checkbox{
  margin: auto;
  display: none;
}

/* Signup/Login CSS */
.Login_Container, .SignUp_Container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2em auto;
  padding: 2em 1em 2em 1em;
  width: 25%;
  border: 1px solid white;
  border-radius: 25px;
}

.Login_Username, .SignUp_Username, .Login_Password, .SignUp_Password, .SignUp_ConfirmPassword, .SignUp_Email{
  font-size: 1.5em;
  margin: 0.5em 0.5em 0.25em 0.5em,;
} 

.LoginRegisterInputFields {
  font-size: 1.5rem;
  width: 75%;
}

.Login_Button, .SignUp_Button {
  border-radius: 25px;
  padding: 0.5em 1em 0.5em 1em;
  font-size: 1em;
  text-align: center;
  color: white;
  border: none;
  background-color: rgb(196,30,58);
  transition-duration: 0.2s;
}

.Login_Button:hover, .SignUp_Button:hover{
  background-color: rgb(159, 26, 49);
  cursor: pointer;
}

.ErrorMessage {
  text-align: center;
  min-height: 4em;
  padding: 0.25em;
  margin: 0.25em;
  color: red;
  font-size: 1em;
}

